import axios from "axios";
import { selectedCourseURL, userDetailURL } from "../../api";

export const loadSelectedCourse = (id, user_id) => async (dispatch) => {
  dispatch({
    type: "LOADING_SELECTED_COURSE",
  });
  const CourseData = await axios.get(selectedCourseURL(id));
  const AllUser = await axios.get(userDetailURL());
  const MOD = AllUser.data.map(function (user) {
    return {
      _id: user._id,
      name: user.name,
      email: user.email,
      online: false,
    };
  });
  let studentInClass = CourseData.data.studentList.map(
    (student) => student.user_id
  );
  studentInClass.push(CourseData.data.classDetail.author_id);

  const enrolled = studentInClass.includes(user_id);

  const CanEnrollUser = MOD.filter(function (user) {
    return !studentInClass.includes(user._id);
  });
  const UserInClass = MOD.filter(function (user) {
    return studentInClass.includes(user._id);
  });

  dispatch({
    type: "GET_SELECTED_COURSE",
    payload: {
      selected: CourseData.data.classDetail,
      courseAuthor: CourseData.data.authorName,
      users: UserInClass,
      enrollList: CanEnrollUser,
      enrolled,
    },
  });
};

export const onlineStatus = (onlineArray, users) => async (dispatch) => {
  const onlineUserArray = onlineArray.map((x) => x.userobject._id);
  const newUser = users.map(function (user) {
    if (onlineUserArray.includes(user._id)) return { ...user, online: true };
    else return user;
  });
  dispatch({
    type: "ONLINE_ON_COURSE",
    payload: {
      users: newUser,
    },
  });
};

export const unloadCourse = () => async (dispatch) => {
  dispatch({
    type: "UNLOAD_SELECTED_COURSE",
  });
};
