const initialState = {
  allUsers: [],
  isLogin: false,
};

const userReducer = (state = initialState, action) => {
  switch (action.type) {
    case "GET_ALL_USER":
      return {
        ...state,
        allUsers: action.payload.allUsers,
        isLoading: true,
      };
    default:
      return { ...state };
  }
};

export default userReducer;
