const initialState = {
  message: [],
  isLoading: true,
};

const authorReducer = (state = initialState, action) => {
  switch (action.type) {
    case "ADD_MSG":
      return {
        ...state,
        message: [...state.message, action.payload.message],
        isLoading: false,
      };
    default:
      return { ...state };
  }
};

export default authorReducer;
