import axios from "axios";
import { loginURL, signupURL, refreshLoginURL } from "../../api";
//IMPORT DECODE JWT
import jwtDecode from "jwt-decode";

export const Login = (email, password) => async (dispatch) => {
  await axios
    .post(loginURL(), { email, password })
    .then((res) => {
      const decoded = jwtDecode(res.data);
      dispatch({
        type: "USER_LOGIN",
        payload: {
          user: decoded,
          token: res.data,
          isLogin: true,
        },
      });
      dispatch({ type: "CLEAR_ERROR" });
      localStorage.setItem("token", res.data);
      localStorage.setItem("user", JSON.stringify(decoded));
    })
    .catch(function (err) {
      // console.log(err.response.data);
      dispatch({
        type: "ERROR_LOGIN",
        payload: {
          error: err.response.data,
        },
      });
    });
};

export const RefreshLogin = () => async (dispatch) => {
  const token = localStorage.getItem("token");
  axios
    .post(refreshLoginURL(), {}, { headers: { "auth-token": token } })
    .then((res) => {
      const decoded = jwtDecode(res.data);
      dispatch({
        type: "USER_LOGIN",
        payload: {
          user: decoded,
          token: res.data,
          isLogin: true,
        },
      });
      dispatch({ type: "CLEAR_ERROR" });
      localStorage.setItem("token", res.data);
      localStorage.setItem("user", JSON.stringify(decoded));
    })
    .catch(function (err) {
      // console.log("ERREOR");
      dispatch({
        type: "ERROR_LOGIN",
        payload: {
          error: err.response,
        },
      });
    });
};

export const Register = (
  { name, email, password, is_admin },
  callback
) => async (dispatch) => {
  await axios
    .post(signupURL(), { name, email, password, is_admin })
    .then((res) => {
      callback();
      dispatch({
        type: "ERROR_REGISTER",
        payload: {
          register_error: "SUCCESS",
        },
      });
      dispatch({ type: "CLEAR_ERROR" });
    })
    .catch((err) => {
      dispatch({
        type: "ERROR_REGISTER",
        payload: {
          register_error: err.response.data,
        },
      });
      return false;
    });
};

export const Logout = () => async (dispatch) => {
  localStorage.clear();
  dispatch({
    type: "USER_LOGOUT",
  });
};

export const ClearError = () => async (dispatch) => {
  dispatch({
    type: "CLEAR_ERROR",
  });
};
